  top: 0;
  width: 100%;
  height: 6.4rem;
  z-index: 999;
  transition: all 0.3s ease-out;
}
#header .header-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 6rem;
}
