  #header .header-inner {
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 0 1.8rem;
    overflow: hidden;
  }
}
#header .header-inner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 4px 28px 0px rgba(0, 0, 0, 0.04);
