  height: 6.4rem;
}
#header .header-inner .site-logo .site-name {
  display: block;
  width: 10.3rem;
  height: 3.9rem;
  background-size: 100%;
}
@media (max-width: 1023px) {
  #header .header-inner .site-logo .site-name {
    width: 92.02px;
    height: 34.34px;
  }
}
#header .header-inner .site-navi {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  #header .header-inner .site-navi {
    order: 3;
    align-items: flex-start;
    position: absolute;
    top: 6.4rem;
    height: calc(100vh - 6.4rem);
    margin: 0 -1.8rem;
    overflow: auto;
    display: none;
  }
}
#header .header-inner .site-navi .depth1-list {
  position: relative;
